import React from "react";
import { Route, Routes } from "react-router-dom";
import { GeneralContextProvider } from "./GeneralContext";

import Apps from "./Apps";
import Funds from "./Funds";
import Holdings from "./Holdings";

import Orders from "./Orders";
import Positions from "./Positions";
import Summary from "./Summary";
import WatchList from "./WatchList";

const Dashboard = () => {
  return (
    <div className="dashboard-container">
      <GeneralContextProvider>
        <WatchList />
      </GeneralContextProvider>
      <div className="content">
        <Routes>
          <Route exact path="zerodha_dashboard/" element={<Summary />} />
          <Route path="zerodha_dashboard/orders" element={<Orders />} />
          <Route path="zerodha_dashboard/holdings" element={<Holdings />} />
          <Route path="zerodha_dashboard/positions" element={<Positions />} />
          <Route path="zerodha_dashboard/funds" element={<Funds />} />
          <Route path="zerodha_dashboard/apps" element={<Apps />} />
        </Routes>
      </div>
    </div>
  );
};

export default Dashboard;
